@import '~jinge-material/style/palette.scss';

.demo {
  :global {
    .md-purple-card {
      background: md-get-palette-color(purple, 500);
    }

    .md-orange-card {
      background: md-get-palette-color(orange, 500);
    }

    .md-card {
      width: 320px;
      margin: 4px;
      display: inline-block;
      vertical-align: top;
    }
  }
}
